<template>
    <div class="xiaozu">
        <TopLink></TopLink>
        <div class="clear"></div>
        <img src="images/timg.gif" class="loding" v-if="xiaozuData.length==0">
        <div v-else>
            <LoginLink></LoginLink>
            <xiaozuList :xiaozuData="xiaozuDataA" title="租房找室友" aData="更多相关小组"></xiaozuList>
            <xiaozuList :xiaozuData="xiaozuDataB" title="租房找室友" aData="来聊五块钱"></xiaozuList>
            <xiaozuList :xiaozuData="xiaozuDataC" title="租房找室友" aData="更多相关小组"></xiaozuList>
        </div>
    </div>
</template>
<script>
import TopLink from "@/components/toplink/toplink.vue"
import LoginLink from "@/components/loginLink/loginLink.vue"
import xiaozuList from "@/pages/xiaozu/xiaozuList/xiaozuList.vue"
export default {
    data(){
        return{
            xiaozuData:[]
        }
    },
    components:{
        TopLink,LoginLink,xiaozuList
    },
    mounted() {
        this.axios({
            url:"/api/xiaozu/xiaozu",
            method:"get"
        }).then(res=>{
            this.xiaozuData = res.data.data;
        })
    },
    computed:{
        xiaozuDataA(){
            return this.xiaozuData.slice(0,3)
        },
        xiaozuDataB(){
            return this.xiaozuData.slice(3,6)
        },
        xiaozuDataC(){
            return this.xiaozuData.slice(6,9)
        },
    }
}
</script>
<style scoped>
    .xiaozu{
        padding-bottom: 0.2rem;
    }
</style>